<template>
    <flex-column>
        <flex-column-header>
            <page-header class="border-bottom">
                <template v-slot:left>
                    <h5>{{contact.title}} <span class="muted">// Timeline</span></h5>
                </template>
            </page-header>
        </flex-column-header>
        <fluro-page-preloader v-if="loading" contain />
        <template v-else>
            <!-- <flex-column-body> -->
                <fluro-timeline v-model="array">
                    <template v-slot:card="{entry}">
                        <div class="timeline-card">
                            <realm-bar :realm="entry.realms"/>
                            <label>{{entry.title}}</label>
                            <!-- <pre>{{entry}}</pre> -->
                            <!-- } -->
                        </div>
                    </template>
                </fluro-timeline>
            <!-- </flex-column-body> -->
        </template>
    </flex-column>
</template>
<script>
import {FluroTimeline,RealmBar} from 'fluro-vue-ui';


export default {
    components: { FluroTimeline, RealmBar },
    props: {
        contact: {
            type: Object,
            required: true,
        }
    },
    data() {
        return {
            loading: true,
        }
    },
    asyncComputed: {
        array: {
            default: [],
            get() {

                var self = this;
                self.loading = true;

                var contactID = self.$fluro.utils.getStringID(self.contact);
                if (!contactID) {
                    return Promise.resolve([])
                }



                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get(`/contact/${contactID}/timeline`).then(function(res) {

                            resolve(res.data);
                            self.loading = false;
                        })
                        .catch(function(err) {
                            reject(err);
                            self.loading = false;

                        });
                    // https://api.staging.fluro.io/info/checkins?contact=592e50389d9129595a75cc4e
                })
            }
        }
    }
}
</script>
<style lang="scss">

.timeline-card {
    display: block;
    background: #fff;
    padding: 15px;
    position: relative;
    @extend .border-bottom;

    &:last-child {
        border-bottom:none;
    }
}
</style>